body {
  margin: 0;
}

.bodyBG {
  background-color: rgb(228, 228, 228);
}

.darkMode {
  filter: invert(1) hue-rotate(180deg);
  -moz-filter: invert(1) hue-rotate(180deg);
  -webkit-filter: invert(1) hue-rotate(180deg);
  -ms-filter: invert(1) hue-rotate(180deg);
}

.loadingGIF {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loadingGIF img {
  width: 100px;
  height: 100px;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.page-header {
  background-color: black;
  box-shadow: 0 20px 18px 0 rgb(0 0 0/50%);
  color: white;
  text-align: center;
  margin: auto;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  padding-top: 20px;
  padding-bottom: 30px;
}

.tt-table th,
.tt-table td {
  vertical-align: middle;
}

.tt-table td {
  white-space: normal;
  min-width: 100px;
  max-width: 160px;
  max-height: 160px;
  min-height: 160px;
  height: 130px;
  vertical-align: middle;
}

.currentWeekDay {
  background-color: tan !important;
}

.tt-table {
  border: 3px solid #000;
  background-color: #fff;
  table-layout: fixed;
}

.tt-table td:hover {
  box-shadow: 0 0 10px 1px #000 inset;
}

.tt-table td:hover .pTag {
  display: none;
}

.tt-table td:hover .join-btn {
  transition: 0.5 ease;
  display: block;
  margin: auto;
}

.join-btn {
  border: 1px solid #000;
  display: none;
}

.flex-container {
  width: 100%;
  padding-top: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: auto;
  justify-content: space-evenly;
  margin-bottom: 70px;
}

.card {
  margin-top: 20px;
  margin-bottom: 20px;
}

.card-body {
  padding: 25px;
}

.card .btn {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.far {
  font-size: 25px;
  cursor: pointer;
}

.data-table {
  border: 0px solid white;
}

td {
  vertical-align: middle;
  white-space: nowrap;
}

.dayTimeTable td {
  border: 1px solid lightgray;
}

.currentDay td {
  border: 2px solid black !important;
  font-weight: 600;
}

.animate-cp-logo {
  animation: animeCP 0.5s linear forwards;
}

@keyframes animeCP {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1.3);
  }
  75% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

.currentLecture {
  background-color: lightgreen !important;
  font-weight: 700 !important;
}

.noLecture {
  background-color: yellow;
}

::-webkit-scrollbar {
  width: 16px;
}

::-webkit-scrollbar-track {
  background-color: rgb(228, 228, 228);
}

::-webkit-scrollbar-thumb {
  background-color: black;
}

.footerDiv .btnPF {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1;
}

.footerDiv .btn-darkMode {
  position: fixed;
  bottom: 20px;
  left: 20px;
  z-index: 1;
}

.btn-darkMode:focus {
  box-shadow: none;
}

.footerDiv .fas,
.footerDiv .far {
  font-size: 30px;
  color: #000;
  /* margin: 20px; */
}

.btnPF:focus {
  box-shadow: none;
}

[data-tooltip-content] {
  position: relative;
}

[data-tooltip-content]::after {
  content: attr(data-tooltip-content);
  padding: 10px;
  border-radius: 0.25rem;
  margin-left: 1rem;
  position: absolute;
  white-space: nowrap;
  background-color: #000;
  color: #fff;
  right: 125%;
  bottom: 5%;
}

[data-tooltip-content]::before {
  content: "";
  width: 0;
  position: absolute;
  border-right: 1rem solid #000;
  border-top: 1rem solid transparent;
  border-bottom: 1rem solid transparent;
  right: 100%;
  bottom: 17%;
  transform: rotateY(180deg);
  pointer-events: none;
}

[data-tooltip-content]::after,
[data-tooltip-content]::before {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

[data-tooltip-content]:hover::after,
[data-tooltip-content]:hover::before {
  opacity: 1;
  visibility: visible;
}
